function buildIncrementalPrompt(
  player: Player,
  gameState: GameState,
  communityCards: Card[],
  playerProfile: any
): string {

  return `**:**
- **:** ${gameState.phase}
- **:** ${gameState.activePlayerIndex}
- **:** [${communityCards.map(c => `${c.rank}${c.suit}`).join(', ')}]
- **:** [${player.holeCards?.map(c => `${c.rank}${c.suit}`).join(', ') || ''}]
- **:** ${gameState.pot}
- **:** ${player.chips}
- **:** ${gameState.currentBet}

${playerProfile ? `**:** ${playerProfile.actions.length}` : ''}

GTO

 {"action": "", "amount": , "reasoning": ""}`;
}

