function buildCompressedGamePrompt(
  player: Player,
  gameState: GameState,
  communityCards: Card[],
  playerProfile: any
): string {

  return `**:**
${player.name}AI

**:**
- : ${gameState.phase} | : ${gameState.pot} | : ${player.chips}
- : [${communityCards.map(c => `${c.rank}${c.suit}`).join(', ')}]
- : [${player.holeCards?.map(c => `${c.rank}${c.suit}`).join(', ') || ''}]
- : ${gameState.currentBet} | : ${gameState.activePlayerIndex}

**:** GTO

 {"action": "fold/check/call/raise/all-in", "amount": , "reasoning": ""}`;
}

//  JSON - 
function parseOptimizedDecisionJSON(text: string): { action: string; amount?: number } | null {
  console.log(` ===== JSON =====`);
  console.log(` :`, text);

  try {
    // 1: JSON
    const jsonMatches = text.match(/\{[^{}]*(?:\{[^{}]*\}[^{}]*)*\}/g);
    if (jsonMatches && jsonMatches.length > 0) {
      // JSON
      const lastJson = jsonMatches[jsonMatches.length - 1];
      console.log(` JSON: ${lastJson}`);

      try {
        const decision = JSON.parse(lastJson);
        if (decision.action && ['fold', 'check', 'call', 'raise', 'all-in', 'bet'].includes(decision.action)) {
          // betraise
          if (decision.action === 'bet') {
            decision.action = 'raise';
          }
          console.log(` JSON:`, decision);
          return {
            action: decision.action,
            amount: decision.amount || 0
          };
        }
      } catch (e) {
        console.log(` JSON: ${e.message}`);
      }
    }

    // 2: actionJSON
    const actionJsonPattern = /\{[^{}]*"action"[^{}]*\}/g;
    const actionMatches = Array.from(text.matchAll(actionJsonPattern));

    for (const match of actionMatches) {
      try {
        let jsonStr = match[0];

        // JSON
        jsonStr = jsonStr
          .replace(/'/g, '"')
          .replace(/(\w+):/g, '"$1":')
          .replace(/,\s*}/g, '}')
          .replace(/,\s*]/g, ']')
          .replace(/"\s*(\d+)\s*"/g, '$1'); // 

        console.log(` JSON: ${jsonStr}`);

        const decision = JSON.parse(jsonStr);
        if (decision.action && ['fold', 'check', 'call', 'raise', 'all-in', 'bet'].includes(decision.action)) {
          if (decision.action === 'bet') {
            decision.action = 'raise';
          }
          console.log(` Action JSON:`, decision);
          return {
            action: decision.action,
            amount: decision.amount || 0
          };
        }
      } catch (e) {
        console.log(` Action JSON: ${e.message}`);
      }
    }

    // 3: JSON
    const flexibleJsonPattern = /\{[\s\S]*?\}/g;
    const flexibleMatches = Array.from(text.matchAll(flexibleJsonPattern));

    for (const match of flexibleMatches) {
      try {
        let jsonStr = match[0];

        // 
        jsonStr = jsonStr
          .replace(/[\r\n\t]/g, ' ')
          .replace(/\s+/g, ' ')
          .replace(/'/g, '"')
          .replace(/(\w+)\s*:/g, '"$1":')
          .replace(/:\s*([^",\{\}\[\]]+)(?=\s*[,\}])/g, ':"$1"')
          .replace(/,\s*}/g, '}')
          .replace(/,\s*]/g, ']');

        console.log(` JSON: ${jsonStr}`);

        const decision = JSON.parse(jsonStr);
        if (decision.action && ['fold', 'check', 'call', 'raise', 'all-in', 'bet'].includes(decision.action)) {
          if (decision.action === 'bet') {
            decision.action = 'raise';
          }
          console.log(` JSON:`, decision);
          return {
            action: decision.action,
            amount: decision.amount || 0
          };
        }
      } catch (e) {
        console.log(` JSON: ${e.message}`);
      }
    }

    console.warn(` JSON`);
    return null;
  } catch (error) {
    console.error(` JSON:`, error);
    return null;
  }
}

//   - 
function buildHandHistoryString(gameState: GameState): string {
  const actionsByPhase: { [phase: string]: ActionHistoryItem[] } = {
    preflop: [],
    flop: [],
    turn: [],
    river: []
  };

  // 
  gameState.actionHistory.forEach(action => {
    if (actionsByPhase[action.phase]) {
      actionsByPhase[action.phase].push(action);
    }
  });

  let historyString = '';
  
  Object.entries(actionsByPhase).forEach(([phase, actions]) => {
    if (actions.length > 0) {
      historyString += `[${getPhaseText(phase)}] `;
      actions.forEach(action => {
        //  
        const player = gameState.players.find(p => p.name === action.playerName);
        const position = player ? getPositionName(player, gameState) : '';
        const amountText = action.amount ? ` ${action.amount}` : '';
        historyString += `${action.playerName}(${position}) ${action.action}${amountText}`;
      });
      historyString += '\n';
    }
  });

  return historyString || '';
}

